//节假日分析
<template>
  <div>
    <!-- 面包屑 -->
    <crumbs menu='商场流量' subMenu='节假日分析' /> 
    <el-card>
      <el-row>
            <el-col :span='2' class="flexCenter fontSet">指标选择:</el-col>
            <el-col :span='8'>
              <el-row>
                  <el-col :span='12'>
                    <market @marketGet='marketGetA' :marketSet='marketName' />
                  </el-col>
                  <el-col :span='12' >
                    <index-select @indexGet='indexGet' :indexSet='index'/>
                  </el-col>
              </el-row>
            </el-col>
            <el-col :span='2' class="flexCenter borderSet fontSet">时间:</el-col>
            <el-col :span='6'>
              <el-row>
                  <div class="block" style="width:100%;">
                    <el-date-picker
                        @change="chartChange"
                        style="width:100%;"
                        :disabled='datePickerAble'
                        v-model="timeRange"
                        type="daterange"
                        :shortcuts="shortcuts"
                        range-separator="至"
                        :clearable='false'
                        :disabled-date="disabledDate"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        value-format="yyyy-MM-dd"
                        align="right">
                    </el-date-picker>
                  </div>
              </el-row>
            </el-col>
            <el-col :span='4' :offset="1">
          <!-- <el-button-group> -->
              <el-button type="primary" icon="el-icon-search" @click='onQuery' >查询</el-button>
              <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
              <!-- <el-button type="primary" icon="el-icon-document"  @click="dialogFormVisible = true">导出</el-button> -->
            <!-- </el-button-group> -->
        </el-col>
          </el-row>
    </el-card>
    <el-card header="流量对比" style="font-weight: bold;margin-top:10px">
                <template #header>
                  <el-row>
                    <el-col :span='10' style="line-height: 40px;">
                      <span>节假日分析</span>
                    </el-col>
                  </el-row>
                </template>
                <div ref="holiday" style="height: 390px;width:100%;" >
                </div>
              </el-card>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import IndexSelect from '../pages/indexSelect/IndexSelect'
import * as echarts from 'echarts'
import axios from 'axios'
export default {
  data(){
    return {
      disabledDate:(time)=>{
            return time.getTime()>this.$moment()
        },
      indexName:'客流量',
      screenWidth: document.body.clientWidth,   
      marketName:'万达',
      index:[{
                value: '客流量',
                label: '客流量',
              }, {
                value: '集客力',
                label: '集客力',
              }, {
                value: '提袋率',
                label: '提袋率',
              }, {
                value: '游逛深度',
                label: '游逛深度',
              }]
      ,
      chart:{},
      chartData:[],
      holidayData:[],
      datePickerAble:false,
      timeRange:'',
      time:'近7日',
      shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [this.$moment().subtract(7, 'days'), end]
                })()
              }, {
                text: '最近一月',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [this.$moment().subtract(1, 'months'), end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [this.$moment().subtract(1, 'years'), end]
                })()
              }],
      holidayName:[],
      holidayAll:['国庆节','中秋节','端午节','劳动节','清明节','中秋节','春节','圣诞节','元旦']
      
    }
    
  },
  components:{
    crumbs:Crumbs,
    // areaSelect:AreaSelect,
    market:MarketSelect,
    // smallTag

    indexSelect:IndexSelect,
  },
  methods:{
    marketGet(val){
      this.marketName=val
    },
    indexGet(val){
      this.indexName=val
    },
    timeIntval(startTime,endTime){
          let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD')
          endTime = moment(endTime).format('YYYY-MM-DD')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('YYYY-MM-DD'))
            startTime = moment(startTime).add(1, 'days').format('YYYY-MM-DD')
          }
          arr.push(moment(endTime).format('YYYY-MM-DD'))
          return arr
      },
    initChart(chart,data){
            let myChart =echarts.init(chart);
            let option = {
                  title:{
                    show:true,
                    text:this.$moment(this.timeRange[0]).format('YYYY-MM-DD') +'至'+this.$moment(this.timeRange[1]).format('YYYY-MM-DD')+' 节假日'+this.indexName,
                    x:'center',
                    y:'top',
                    textStyle:{
                      fontSize:'14',
                      fontWeight:'bolder'
                    }
                  },
                  tooltip: {
                      position: 'top',
                      axisPointer: {
                          type: 'cross',
                          crossStyle: {
                              color: '#999'
                          }
                      },
                  },
                  legend: {
                      data:['节假日'],
                      top:'top',
                      left:'left'
                  },
                  toolbox: {
                      show: true,
                      feature: {
                          // dataView: {show: true, readOnly: false},
                          // magicType: {show: true, type: ['line', 'bar']},
                          // restore: {show: true},
                          saveAsImage: {show: true}
                      }
                  },
                  dataZoom: [{
                      type: 'slider',
                      xAxisIndex: [0],
                  }, {
                      type: 'slider',
                      yAxisIndex: [0],
                  },{
                    type:'inside',
                      xAxisIndex: [0],
                  },{
                    type:'inside',
                      yAxisIndex: [0],
                  }],
                  calculable: true,
                  xAxis: [
                      {
                          type: 'category',
                          data: this.holidayName
                      }
                  ],
                  yAxis: [
                      {
                          type: 'value'
                      }
                  ],
                  series: data
              };
            console.log(option.legend.data)
            console.log(option.series)
            myChart.setOption(option);
            return myChart
    },
    onQuery(){
      this.chartChange()
    },
    chartChange(){
      this.chart.clear()
      let start=this.$moment(this.timeRange[0]).startOf('year').format('YYYY')
      let end=this.$moment(this.timeRange[1]).startOf('year').format('YYYY')
      let axiosArr=[]
      console.log(this.timeRange)
      axios.all([])
      for(start;start<=end;start++){
        axiosArr.push(axios(`https://api.apihubs.cn/holiday/get?year=${start}&holiday=22,11,44,55,66,77,88,97&cn=1&size=366`))
        console.log(start)
      }
      console.log(axiosArr)
      // console.log(this.timeIntval(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD')))
      axiosArr.push(axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getHoliday?lg=${this.timeIntval(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD')).length}`))
      axios.all(axiosArr).then(
        axios.spread((...res)=>{
          console.log(res[1])
          let data=res[([...res].length-1)].data.list
          let allDate=this.timeIntval(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'))
          console.log(data)
          console.log(allDate)
          let propsName=''
          if(this.indexName=='客流量'){
                  propsName='flow'
                }else if(this.indexName=='集客力'){
                  propsName='getPower'
                }else if(this.indexName=='提袋率'){
                  propsName='rate'
                }else if(this.indexName=='游逛深度'){
                  propsName='deep'
                }
          
          let holidayName=[]
          let holidayValue=[]
          for(let i=0;i<[...res].length-1;i++){
            console.log(i)
            let holidayData=res[i].data.data.list
            console.log(holidayData)
            holidayData.map(item=>{
                        if(this.$moment(item.date,'YYYYMMDD').isBetween(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'),'day','[]')){
                          if(holidayName.indexOf(item.holiday_cn)!=-1){
                            holidayValue[holidayName.indexOf(item.holiday_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
                          }else{
                            holidayName.push(item.holiday_cn)
                            holidayValue.push(data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))])
                          }
                          if(item.holiday_cn!=item.holiday_or_cn&&this.holidayAll.indexOf(item.holiday_or_cn)!=-1){
                            if(holidayName.indexOf(item.holiday_or_cn)!=-1){
                              holidayValue[holidayName.indexOf(item.holiday_or_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
                              holidayValue[holidayName.indexOf(item.holiday_or_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
                            }else{
                              holidayName.push(item.holiday_or_cn)
                              holidayValue.push(data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))])
                          }
                          }
                        }
                      })
                  
          }
          console.log(holidayValue)
            let totalWeekend=0
        for(let i=0;i<allDate.length;i++){
          if(this.$moment(allDate[i]).isoWeekday()==6||this.$moment(allDate[i]).isoWeekday()==7){
            totalWeekend+=data[propsName][i]
          }
        }
        if(totalWeekend!=0){
          holidayName.push('周末')
          holidayValue.push(totalWeekend)
        }
        let start=this.$moment(this.timeRange[0]).startOf('year').format('YYYY')
        let end=this.$moment(this.timeRange[1]).startOf('year').format('YYYY')
        for(let i=start;i<=end;i++){
          if(this.$moment(this.$moment(i).year()+`-11-11`).isBetween(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'),'day','[]')){
            holidayName.push('双十一')
            holidayValue.push(data[propsName][allDate.indexOf(this.$moment(this.$moment(i).year()+`-11-11`).format('YYYY-MM-DD'))])
          }
          if(this.$moment(this.$moment(i).year()+`-12-25`).isBetween(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'),'day','[]')){
            holidayName.push('圣诞节')
            holidayValue.push(data[propsName][allDate.indexOf(this.$moment(this.$moment(i).year()+`-11-11`).format('YYYY-MM-DD'))])
          }
        }
        console.log(holidayName)
        this.holidayName=holidayName
        this.chartData=[{
              name: '节假日',
              type: 'bar',
              data: holidayValue,
              markPoint: {
                  data: [
                      {type: 'max', name: '最大值'},
                      {type: 'min', name: '最小值'}
                  ]
              },
              markLine: {
                  data: [
                      {type: 'average', name: '平均值'}
                  ]
              }
          }]
        this.chart=this.initChart(this.$refs.holiday,this.chartData)
        })
      )
  
    },
    reSet(){
      this.chartChange()
      this.timeRange=[this.$moment().startOf('year'),this.$moment()]
      this.marketName='万达'

    }
  },
  mounted(){
    const that = this
        window.onresize = () => {
        return (() => {
            window.screenWidth = document.body.clientWidth
            that.screenWidth = window.screenWidth
        })()
        }
    this.timeRange=[this.$moment().startOf('year'),this.$moment()]
    let start=this.$moment(this.timeRange[0]).startOf('year').format('YYYY')
    let end=this.$moment(this.timeRange[1]).startOf('year').format('YYYY')
    let axiosArr=[]
    
    axios.all([])
    for(start;start<=end;start++){
      axiosArr.push(axios(`https://api.apihubs.cn/holiday/get?year=${start}&holiday=22,11,44,55,66,77,88,97&cn=1&size=366`))
    }
    axiosArr.push(axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getHoliday?lg=${this.timeIntval(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD')).length}`))
    axios.all(axiosArr).then(
      axios.spread((...res)=>{
        let data=res[1].data.list
        let allDate=this.timeIntval(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'))
        let propsName=''
        if(this.indexName=='客流量'){
                propsName='flow'
              }else if(this.indexName=='集客力'){
                propsName='getPower'
              }else if(this.indexName=='提袋率'){
                propsName='rate'
              }else if(this.indexName=='游逛深度'){
                propsName='deep'
              }
        let holidayData=res[0].data.data.list
        let holidayName=[]
        let holidayValue=[]
        holidayData.map(item=>{
          if(this.$moment(item.date,'YYYYMMDD').isBetween(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'),'day','[]')){
            if(holidayName.indexOf(item.holiday_cn)!=-1){
              holidayValue[holidayName.indexOf(item.holiday_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
              holidayValue[holidayName.indexOf(item.holiday_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
            }else{
              holidayName.push(item.holiday_cn)
              holidayValue.push(data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))])
            }
            if(item.holiday_cn!=item.holiday_or_cn&&this.holidayAll.indexOf(item.holiday_or_cn)!=-1){
              if(holidayName.indexOf(item.holiday_or_cn)!=-1){
                holidayValue[holidayName.indexOf(item.holiday_or_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
                holidayValue[holidayName.indexOf(item.holiday_or_cn)]+=data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))]
              }else{
                holidayName.push(item.holiday_or_cn)
                holidayValue.push(data[propsName][allDate.indexOf(this.$moment(item.date,'YYYYMMDD').format('YYYY-MM-DD'))])
            }
            }
          }

        })
      let totalWeekend=0
      for(let i=0;i<allDate.length;i++){
        if(this.$moment(allDate[i]).isoWeekday()==6||this.$moment(allDate[i]).isoWeekday()==7){
          totalWeekend+=data[propsName][i]
        }
      }
      if(totalWeekend!=0){
        holidayName.push('周末')
        holidayValue.push(totalWeekend)
      }
      let start=this.$moment(this.timeRange[0]).startOf('year').format('YYYY')
      let end=this.$moment(this.timeRange[1]).startOf('year').format('YYYY')
      for(let i=start;i<=end;i++){
        if(this.$moment(this.$moment(i).year()+`-11-11`).isBetween(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'),'day','[]')){
          holidayName.push('双十一')
          holidayValue.push(data[propsName][allDate.indexOf(this.$moment(this.$moment(i).year()+`-11-11`).format('YYYY-MM-DD'))])
        }
        if(this.$moment(this.$moment(i).year()+`-12-25`).isBetween(this.$moment(this.timeRange[0]).format('YYYY-MM-DD'),this.$moment(this.timeRange[1]).format('YYYY-MM-DD'),'day','[]')){
            holidayName.push('圣诞节')
            holidayValue.push(data[propsName][allDate.indexOf(this.$moment(this.$moment(i).year()+`-11-11`).format('YYYY-MM-DD'))])
          }
      }
      this.holidayName=holidayName
      this.chartData=[{
            name: '节假日',
            type: 'bar',
            data: holidayValue,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
            markLine: {
                data: [
                    {type: 'average', name: '平均值'}
                ]
            }
        }]
      this.chart=this.initChart(this.$refs.holiday,this.chartData)
      })
    )
  },
  watch:{ 
    screenWidth(val){
        // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
        if(!this.timer){
            // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
            this.screenWidth = val
            this.timer = true
            let that = this
            setTimeout(function(){
                // 打印screenWidth变化的值
                this.chart.resize()
                that.timer = false
            },400)
        }
      
    },
    indexName(){
      this.chartChange()
    },
    marketName(){
      this.chartChange()
    }
  }
}
</script>
<style scoped>
  .borderSet{
    /* border-right: 2px solid black; */
    border-left: 2px solid #C0C4CC;
  }
  .flexCenter{
    display: flex;
    align-items: center; /*垂直方向居中*/
    justify-content: space-around;  /*水平方向居中*/
    /* flex-wrap: wrap; */
  }
  .flexJus{
    display: flex;
    justify-content: space-around;  /*水平方向居中*/
  }
 .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .card-header span{
    font-size: 20px;
    font-weight: bolder;
  }
  .fontSet{
     font-size: 18px;
    font-weight: bolder;
  }
</style>